// To sort posts in descending order, by date replace: 
//	allMarkdownRemark {
// with 
// 	allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }). 
// Note: There are 3 underscores between frontmatter and date. 


export const query = graphql`
  query {
    allMarkdownRemark (sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }
  }
`


